  flex-direction: column;
  align-items: center;
  margin: 50px 0;
}

.card{
	margin: 0 2% !important;

}

.infected{
	color: #007bff !important;
	background-color: #d4e9ff !important;

}
.recovered{
	color: #28a745 !important;
